import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/home/HomeView.vue";
import TestView from "../views/test/TestView.vue";
import ComponentView from "../views/component/ComponentView.vue";
import UploadView from "../views/upload/UploadView.vue";

const routes = [
  {
    entry: "./src/views/home/main.js",
    path: "/",
    name: "Home",
    component: HomeView,
  },
  {
    entry: "./src/views/component/main.js",
    path: "/components",
    name: "Components",
    component: ComponentView,
  },
  {
    entry: "./src/views/upload/main.js",
    path: "/upload",
    name: "Upload",
    component: UploadView,
  },
  {
    entry: "./src/views/test/main.js",
    path: "/test",
    name: "test",
    component: TestView,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
